* {
    box-sizing: border-box;
    outline: none; }

body, html {
    background: white;
    color: $fontColor;
    font-family: $font-family;
    font-size: 16px;
    min-width: 320px;
    max-width: 100%;
    margin: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: 100%;
    background: rgb(218,217,236); }
.body_fail {
    background: #f31a1a;
    .page-container__frame {
        background: #f31a1a; } }
.body_success {
    background: #6dff72;
    .page-container__frame {
        background: #6dff72; } }
.page-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    max-width: 540px;
    max-height: 540px;
    width: 100%;
    height: 100%; }
.page-container__frame {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgb(218,217,236);
    z-index: -1;
    opacity: 0; }
