.finish {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-left: 15px;
    padding-right: 15px; }
.finish__title {
    font-size: 30px;
    font-weight: 700;
    font-family: Circe;
    color: #000;
    margin: 0; }
.finish__list {
    list-style: none;
    padding-left: 0;
    margin-top: 40px; }
.finish__item {
    color: #000;
    margin-top: 15px;
    font-size: 16px;
    font-weight: 400;
    font-family: Circe;
    &:first-child {
        margin-top: 0; } }
.finish__item_l {
    font-weight: bold; }
.finish__btn {
    margin-top: 5px;
    background-color: #000;
    border-radius: 5px;
    color: #fff;
    padding: 15px;
    border: none;
    font-size: 17px;
    font-family: Circe;
    font-weight: 400;
    cursor: pointer;
    border: 1px solid #000;
    transition: 0.3s;
    &:hover {
        background-color: #fff;
        color: #000; } }
