.game__timer {
    font-size: 28px;
    font-weight: 300;
    font-family: 'Circe';
    position: absolute;
    left: 50%;
    transform: translateX(-50%); }

.game__timer-r_red {
    color: #f31a1a; }

.game__birds {
    display: flex;
    justify-content: space-between;
    padding-left: 10px;
    padding-right: 10px;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    max-width: 380px;
    transform: translate(-50%,-50%);
    transform-origin: 50% 50%;
    opacity: 0; }
.game__wrap-bird {
    width: calc( 100% / 7 - 5px );
    &:nth-child(1),&:nth-child(7) {
        margin-top: 30%; }
    &:nth-child(2),&:nth-child(6) {
        margin-top: 20%; }
    &:nth-child(3),&:nth-child(5) {
        margin-top: 10%; } }
.game__bird {
    width: 100%;
    height: auto;
    display: block; }
.game__main-bird_reverse {
    transform: rotate(90deg); }
.game__birds_right, .game__birds_left {
    @include rh(750) {
        max-width: 320px; } }

.game__birds_left, .game__birds_top, .game__birds_right, .game__birds_bottom {
    opacity: 1; }

.game__birds_right {
    transform: translate(-50%, -50%) rotate(90deg); }
.game__birds_left {
    transform: translate(-50%, -50%) rotate(-90deg); }
.game__birds_top {
    transform: translate(-50%, -50%) rotate(0deg); }
.game__birds_bottom {
    transform: translate(-50%, -50%) rotate(180deg); }

.game__main-bird_right {
    transform: rotate(90deg); }
.game__main-bird_left {
    transform: rotate(-90deg); }
.game__main-bird_top {
    transform: rotate(0deg); }
.game__main-bird_bottom {
    transform: rotate(180deg); }

.game__birds_right {
    .game__main-bird_right {
        transform: rotate(0deg); }
    .game__main-bird_left {
        transform: rotate(-180deg); }
    .game__main-bird_top {
        transform: rotate(-90deg); }
    .game__main-bird_bottom {
        transform: rotate(90deg); } }

.game__birds_bottom {
    .game__main-bird_right {
        transform: rotate(-90deg); }
    .game__main-bird_left {
        transform: rotate(90deg); }
    .game__main-bird_top {
        transform: rotate(180deg); }
    .game__main-bird_bottom {
        transform: rotate(0); } }

.game__birds_left {
    .game__main-bird_right {
        transform: rotate(180deg); }
    .game__main-bird_left {
        transform: rotate(0); }
    .game__main-bird_top {
        transform: rotate(90deg); }
    .game__main-bird_bottom {
        transform: rotate(270deg); } }



.control {
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    width: 140px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end; }
.control__left, .control__right, .control__inner {
    width: calc( 100% / 3 - 5px ); }
.control__left, .control__right, .control__top, .control__bottom {
    height: 26px;
    border: 1px solid #000000;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    transition: background 0.1s;
    cursor: pointer;
    svg {
        width: 16px;
        height: 16px;
        display: block;
        transform-origin: 50% 50%;
        transition: fill 0.1s; } }
.control__bottom {
    margin-top: 5px; }

.control__left {
    svg {
        transform: rotate(90deg); } }
.control__top {
    svg {
        transform: rotate(180deg); } }
.control__right {
    svg {
        transform: rotate(-90deg); } }
.control__bottom {
    svg {
        transform: rotate(0); } }

.control_active {
    background-color: #000;
    svg {
        fill: #fff; } }


.game__countdown {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 90px;
    color: #000000;
    font-weight: bold; }
