.start {
    display: flex;
    justify-content: center;
    align-items: center;

    opacity: 1;
    z-index: 1; }
.start__btn {
    background-color: #000;
    border-radius: 5px;
    color: #fff;
    padding: 15px;
    border: none;
    font-size: 17px;
    font-family: Circe;
    font-weight: 400;
    cursor: pointer;
    border: 1px solid #000;
    transition: 0.3s;
    &:hover {
        background-color: #fff;
        color: #000; } }
